.ac {
  margin: 15px;
}
.ac-container {
  box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
  max-width: 375px;
  min-width: 250px;
  padding: 20px;
  background-color: white;
  border-radius: 10px;
}
.ac-heading {
  margin-bottom: 15px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.chip-action-middle {
  width: 100px;
  height: 1px;
  background: #80808029;
}
.chip {
  margin: 20px 0px;
  display: flex;
  align-items: center;
}
.chip-image {
}
.chip-image span {
  background-color: burlywood;
  margin-right: 10px;
  padding: 12px;
  border-radius: 50px;
  color: white;
  font-weight: bold;
}
.chip-name {
  word-break: break-word;
  margin-bottom: 2px;
  font-weight: bold;
  font-size: 18px;
  line-height: 1.2;
}
.chip-time {
  color: grey;
  font-size: 12px;
}
.ac-action {
  margin-left: 10px;
  display: flex;
}
.ac-action-button {
  box-shadow: rgba(17, 12, 46, 0.15) 0px 48px 100px 0px;
  margin-left: 6px;
  cursor: pointer;
  font-size: 14px;
  background-color: rgb(207, 136, 207);
  padding: 5px;
  color: white;
  border-radius: 50px;
}
.ac-action-button:hover {
  background-color: purple;
  background-color: purple;
}
.ac-heading span {
}
.ac-description {
  color: #000000a3;
  margin-top: 20px;
}
.ac-description p {
  margin: 0px;
  padding: 0px;
  word-wrap: break-word;
  line-height: 1.4;
}
@media (max-width: 730px) {
  .ac-container {
    max-width: 100%;
  }
}
