.IandAViewPort {
}
.IandAViewPort-container {
}
.IandAViewPort-creator {
  margin-top: 4rem;
}
.IandAViewPort-creator-inputs {
}
.IandAViewPort-creator-button {
}
.IandAViewPort-listview {
  margin-top: 2rem;
  overflow-y: scroll;
}
.navigation {
  padding: 8px 30px;
  background-color: white;
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.nav-logo {
  font-size: 25px;
}
.nav-logo span {
  font-family: "Shrikhand", cursive;
}
.nav-credit {
  font-size: 15px;
  color: grey;
}
.nav-credit span {
}
.nav-credit span a {
  color: grey;
  text-decoration: none;
}

.card-display {
  display: flex;
  flex-wrap: wrap-reverse;
  flex-direction: row-reverse;
  justify-content: center;
  margin: 1rem 0rem;
}
@media (max-width: 420px) {
  .nav-logo {
    font-size: 21px;
  }
  .nav-credit {
    font-size: 11px;
    color: grey;
  }
}

/* Creator Popup */
.acc-toogle {
  border: 1px solid purple;
  color: white;
  z-index: 9999999999;
  position: fixed;
  right: 23px;
  bottom: 60px;
  background: rgb(207, 136, 207);
  padding: 6px;
  font-size: 45px;
  cursor: pointer;
  border-radius: 500px;
}
.acc-toogle:hover {
  background: purple;
}
.acc-container {
}
.acc-creator-container {
  margin-left: 22px;
  bottom: 120px;
  right: 23px;
  position: fixed;
}
.acc {
  margin: 5px;
}
.acc-container {
}
.acc-input {
  box-shadow: rgba(0, 0, 0, 0.4) 0px 30px 90px;
  border-radius: 10px;
  margin: 20px 0px;
  padding: 20px;
  background-color: white;
}
.acc-input label {
  font-family: "Poppins", sans-serif;
  font-weight: 700;
  font-size: 1.25rem;
  line-height: 1.75rem;
}
.acc-input input,
.acc-input textarea {
  font-family: "Inter", sans-serif;
  margin-top: 20px;
  -webkit-box-flex: 1;
  flex-grow: 1;
  box-sizing: border-box;
  appearance: none;
  box-shadow: none;
  outline: 0px;
  border: 1px solid #00000012;
  border-radius: 12px;
  height: 48px;
  width: 100%;
  padding: 8px;
  font-size: 16px;
  background-color: rgb(243, 244, 246);
}
.acc-input textarea {
  height: 150px;
}
